<template>
  <v-content v-if="show">
    <v-dialog eager persistent v-model="show" width="800">
      <v-overlay :value="loading">
        <v-progress-circular indeterminate size="64"></v-progress-circular>
      </v-overlay>
      <v-overlay :value="video">
        <v-sheet height="400" width="700">
          <v-row style="height:60px" align="center" justify="space-around">
            <div style="margin-right:50%">
              <v-row align="center">
                <h3 class="exerciseTitle">{{$t("viewDataAndAddClientData.video.exercise")}}:</h3>
                {{viewExercise}}
              </v-row>
            </div>

            <v-btn small fab text @click="closeVideo">
              <v-icon>clear</v-icon>
            </v-btn>
          </v-row>
          <video height="400" width="700" controls>
            <source :src="videoUrl" type="video/mp4" />
            <!-- <source src="movie.ogg" type="video/ogg"> -->
            Your browser does not support the video tag.
          </video>
        </v-sheet>
      </v-overlay>
      <v-sheet>
        <v-tabs grow slider-color="black">
          <v-tab
            class="exercises"
            v-ripple="false"
          >{{$t("viewDataAndAddClientData.tabs.exercises")}}</v-tab>
          <v-tab class="macros" v-ripple="false">{{$t("viewDataAndAddClientData.tabs.macros")}}</v-tab>
          <v-tab class="steps" v-ripple="false">{{$t("viewDataAndAddClientData.tabs.steps")}}</v-tab>
          <v-tab class="weight" v-ripple="false">{{$t("viewDataAndAddClientData.tabs.weight")}}</v-tab>
          <v-tab class="payment" v-ripple="false">{{$t("viewDataAndAddClientData.tabs.payment")}}</v-tab>
          <v-tab class="comments" v-ripple="false">{{$t("viewDataAndAddClientData.tabs.comments")}}</v-tab>

          <v-tab-item>
            <v-overlay :value="loadVideo">
              <v-progress-circular indeterminate size="64"></v-progress-circular>
            </v-overlay>
            <v-row style="padding:5px 0 5px 0;margin:0" justify="center">
              <h3>{{$t("viewDataAndAddClientData.exercises.warmUp")}}</h3>
            </v-row>
            <v-divider></v-divider>
            <div v-for="(exercise, index) in getDayData.warmUpExercises" :key="index">
              <v-row style="margin:0" align="center" justify="center">
                <v-col class="center">
                  <!-- <v-text-field
                    v-model="exercise.name"
                    disabled
                    :full-width="true"
                    filled
                    dense
                    rounded
                    class="fieldStyle"
                  ></v-text-field>-->
                  <div v-if="getDayData.date">{{exercise.exercise}}</div>
                  <div v-else></div>
                </v-col>
                <v-divider vertical></v-divider>

                <v-col>
                  <v-row justify="center" align="center">
                    <v-btn
                      v-if="exercise.hasVideo == true"
                      @click="seeVideoWarmUp(exercise._id,exercise.exercise)"
                    >
                      {{$t("viewDataAndAddClientData.exercises.view")}}
                      <v-icon right>visibility</v-icon>
                    </v-btn>
                    <v-btn v-else>
                      {{$t("viewDataAndAddClientData.exercises.view")}}
                      <v-icon disabled right>visibility</v-icon>
                    </v-btn>

                    <v-btn
                      v-if="exercise.hasVideo != true"
                      style="margin-left:10%"
                      color="blue"
                      @click="pickVideoWarmUp(index, exercise._id)"
                      class="mr-10"
                    >
                      {{$t("viewDataAndAddClientData.exercises.add")}}
                      <v-icon right>backup</v-icon>
                    </v-btn>

                    <v-btn
                      v-if="exercise.hasVideo == true"
                      style="margin-left:10%"
                      color="error"
                      @click="deleteVideoWarmUp(exercise._id)"
                      class="mr-10"
                    >Delete</v-btn>

                    <input
                      type="file"
                      hidden
                      ref="videoWarmUp"
                      accept="video/mp4"
                      @change="onFilePickedWarmUp"
                    />
                  </v-row>
                </v-col>
                <v-divider vertical></v-divider>
              </v-row>
            </div>
            <v-divider></v-divider>
            <v-row style="padding:5px 0 5px 0;margin:0" justify="center">
