                @click="saveData"
                :normal="true"
              >{{$t("viewDataAndAddClientData.save")}}</v-btn>
            </v-row>
          </v-tab-item>
          <v-tab-item>
            <v-textarea
              v-model="clientInput.comments"
              style="padding-top:20px;"
              filled
              rounded
              dense
            ></v-textarea>
            <v-divider></v-divider>
            <v-row
              style="padding:20px 3% 20px 3%"
              align="center"
              justify="space-between"
              no-gutters
            >
              <v-btn
                @click="close"
                color="error"
                :normal="true"
              >{{$t("viewDataAndAddClientData.close")}}</v-btn>
              <v-btn
                color="primary"
                @click="saveData"
                :normal="true"
              >{{$t("viewDataAndAddClientData.save")}}</v-btn>
            </v-row>
          </v-tab-item>
        </v-tabs>
      </v-sheet>
    </v-dialog>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import moment from "moment";
import axios from "axios";
import Cookies from "js-cookie";

export default {
  name: "addDataClient",
  props: ["show", "day", "month", "year"],
  data() {
    return {
      loading: false,
      clientData: {},
      loadVideo: false,
      exerciseId: 0,
      value: 0,
      video: false,
      videoUrl: "",
      viewExercise: "",
      renderVideo: false
    };
  },
  computed: mapGetters([
    "types",
    "values",
    "activeClient",
    "macros",
    "user",
    "getDayData",
    "clientInput",
    "payment"
  ]),
  created() {},
  updated() {
    this.checkForDayData();
  },
  watch: {
    "macros.client.proteins"() {
      this.addCalories();
    },
    "macros.client.carbs"() {
      this.addCalories();
    },
    "macros.client.fats"() {
      this.addCalories();
    }
  },
  methods: {
    addCalories() {
      if (this.user.systemType == 1000) {
        this.macros.client.calories =
          this.macros.client.proteins * 4 +
          this.macros.client.carbs * 4 +
          this.macros.client.fats * 9;
      }
    },
    async deleteVideoWarmUp(exercise) {
      await this.$store.dispatch("deleteExerciseWarmUp", {
        year: this.year,
        month: this.month,
        day: this.day,
        exercise: exercise
      });

      let date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      date = moment(date).format("YYYY-MM-DD");
      await this.$store.dispatch("getExercisesClient", date);
    },
    async deleteVideoWorkout(exercise) {
      await this.$store.dispatch("deleteExerciseWorkout", {
        year: this.year,
        month: this.month,
        day: this.day,
        exercise: exercise
      });

      let date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      date = moment(date).format("YYYY-MM-DD");
      await this.$store.dispatch("getExercisesClient", date);
    },
    async deleteVideoCoolUp(exercise) {
      await this.$store.dispatch("deleteExerciseCoolUp", {
        year: this.year,
        month: this.month,
        day: this.day,
        exercise: exercise
      });

      let date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      date = moment(date).format("YYYY-MM-DD");
      await this.$store.dispatch("getExercisesClient", date);
    },
    seeVideoWarmUp(exercise, exerciseName) {
      this.video = true;
      this.videoUrl = `/api/dayClient/exercises/video/warmUp/${this.year}/${
        this.month
      }/${this.day}/${exercise}?auth=${Cookies.get("token")}`;

      this.viewExercise = exerciseName;

      // console.log(this.$refs.video);

      // console.log("load");
      // this.$refs.video.load();
    },
    seeVideoWorkout(exercise, exerciseName) {
      this.video = true;
      this.renderVideo = true;
      this.videoUrl = `/api/dayClient/exercises/video/workout/${this.year}/${
        this.month
      }/${this.day}/${exercise}?auth=${Cookies.get("token")}`;
      this.viewExercise = exerciseName;
    },
    seeVideoCoolUp(exercise, exerciseName) {
      this.video = true;
      this.renderVideo = true;
      this.videoUrl = `/api/dayClient/exercises/video/coolUp/${this.year}/${
        this.month
      }/${this.day}/${exercise}?auth=${Cookies.get("token")}`;
      this.viewExercise = exerciseName;
    },
    closeVideo() {
      this.video = false;
    },
    pickVideoWarmUp(index, exercise) {
      console.log("Warm up");
      this.exerciseId = exercise;
      this.$refs.videoWarmUp[index].click();
    },
    pickVideoWorkout(index, exercise) {
      console.log("Workout");
      this.exerciseId = exercise;
      this.$refs.videoWorkout[index].click();
    },
    pickVideoCoolUp(index, exercise) {
      console.log("Cool up");
      this.exerciseId = exercise;
      this.$refs.videoCoolUp[index].click();
    },
    onFilePickedWarmUp: async function(event) {
      // this.load = true;
      // await this.$store
      //   .dispatch("uploadImage", event.target.files[0])
      //   .then(async () => {
      //     await this.$store.dispatch("profile").then(() => {
      //       this.load = false;
      //     });
      //   });
      this.loadVideo = true;
      const formData = new FormData();
      formData.append(
        "video",
        event.target.files[0],
        event.target.files[0].name
      );
      let date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      date = moment(date).format("YYYY-MM-DD");
      await axios.post(
        `/api/dayClient/exercises/video/warmUp/${this.year}/${this.month}/${this.day}/${this.exerciseId}`,
        formData,
        {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`
          },
          onUploadProgress: progressEvent => {
            this.value = Math.round(
              (progressEvent.loaded / progressEvent.total) * 100
            );
            console.log(this.value);
          }
        }
      );
      await this.$store.dispatch("getExercisesClient", date);
      this.loadVideo = false;
      this.value = 0;
    },
    onFilePickedWorkout: async function(event) {
      this.loadVideo = true;
      const formData = new FormData();
      formData.append(
        "video",
        event.target.files[0],
        event.target.files[0].name
      );
      let date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      date = moment(date).format("YYYY-MM-DD");
      await axios.post(
        `/api/dayClient/exercises/video/workout/${this.year}/${this.month}/${this.day}/${this.exerciseId}`,
        formData,
        {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`
          },
          onUploadProgress: progressEvent => {
            this.value = Math.round(
              (progressEvent.loaded / progressEvent.total) * 100
            );
          }
        }
      );

      await this.$store.dispatch("getExercisesClient", date);
      this.loadVideo = false;
      this.value = 0;
    },
    onFilePickedCoolUp: async function(event) {
      this.loadVideo = true;
      const formData = new FormData();
      formData.append(
        "video",
        event.target.files[0],
        event.target.files[0].name
      );
      let date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      date = moment(date).format("YYYY-MM-DD");
      await axios.post(
        `/api/dayClient/exercises/video/coolUp/${this.year}/${this.month}/${this.day}/${this.exerciseId}`,
        formData,
        {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`
          },
          onUploadProgress: progressEvent => {
            this.value = Math.round(
              (progressEvent.loaded / progressEvent.total) * 100
            );
          }
        }
      );
      await this.$store.dispatch("getExercisesClient", date);
      this.loadVideo = false;
      this.value = 0;
    },
    checkForDayData() {},
    close() {
      this.client = {};

      this.$emit("closed", this.show);
    },
    async saveData() {
      this.loading = true;

      const date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();
      this.clientData = { ...this.clientInput };

      this.clientData.weight.systemSaved = this.user.systemType;
      this.clientData.steps.systemSaved = this.user.systemType;
      let nullValuesSteps = 0;
      let nullValuesWeight = 0;
      let nullValuesPayment = 0;
      Object.keys(this.clientData.steps).forEach(el => {
        if (el != "systemSaved") {
          if (
            this.clientData.steps[el] == null ||
            this.clientData.steps[el] == ""
          ) {
            nullValuesSteps = nullValuesSteps + 1;
          }
        }
      });

      if (nullValuesSteps == Object.keys(this.clientData.steps).length - 1) {
        delete this.clientData.steps;
      }

      Object.keys(this.clientData.weight).forEach(el => {
        if (el != "systemSaved") {
          if (
            this.clientData.weight[el] == null ||
            this.clientData.weight[el] == ""
          ) {
            nullValuesWeight = nullValuesWeight + 1;
          }
        }
      });

      Object.keys(this.payment).forEach(el => {
        if (this.payment[el] == null || this.payment[el] == "") {
          nullValuesPayment = nullValuesPayment + 1;
        }
      });

      if (nullValuesPayment == 0) {
        this.clientData.payment = { ...this.payment };
      }

      if (nullValuesWeight == Object.keys(this.clientData.weight).length - 1) {
        delete this.clientData.weight;
      }

      if (this.clientData.comments == null || this.clientData.comments == "") {
        delete this.clientData.comments;
      }

      this.clientData.date = moment(date).format("YYYY-MM-DD");
      this.clientData.user = this.user.user;
      this.clientData.macros = { ...this.macros.client };

      await this.$store.dispatch("uploadDayClient", this.clientData);

      this.$emit("updateCalendar");
      this.loading = false;
    }
  }
};
</script>

<style>
