<template>
  <v-content>
    <v-overlay :value="loading">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <addData
      :show="openAddData"
      :day="day"
      :month="month"
      :year="year"
      @closed="closeAddData"
      @updateCalendar="updateCalendar"
    ></addData>
    <viewData
      :show="openViewData"
      :day="day"
      :month="month"
      :year="year"
      @closed="closeViewData"
      @updateCalendar="updateCalendar"
    ></viewData>
    <addDataClient
      :show="openAddDataClient"
      :day="day"
      :month="month"
      :year="year"
      @closed="closeAddDataClient"
      @updateCalendar="updateCalendar"
    ></addDataClient>
    <v-sheet height="50">
      <v-row align="center" justify="space-between">
        <v-col cols="auto">
          <v-btn class="hidden-md-and-down" fab text small color="grey darken-2" @click="prev">
            <v-icon small>mdi-chevron-left</v-icon>
          </v-btn>
        </v-col>
        <v-col cols="auto">
          <h3>{{ $t(`months[${calendarMonth - 1}]`) }} {{ calendarYear }}</h3>
        </v-col>
        <v-col cols="auto">
          <v-btn class="hidden-md-and-down" fab text small color="grey darken-2" @click="next">
            <v-icon small>mdi-chevron-right</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-sheet>
    <v-sheet height="815" width="100%">
      <v-calendar
        ref="calendar"
        v-model="focus"
        :type="calendarType"
        :value="today"
        color="primary"
        :event-color="getEventColor"
        :weekdays="[1, 2, 3, 4, 5, 6, 0]"
        :event-more="eventMore"
        @change="seeChange"
      >
        <template v-slot:day-month="{date}">{{date}}</template>

        <template v-slot:day="{ present, future, day, month, year, weekday,date }">
          <div v-if="daysData[day]">
            <div v-if="month == daysData[day].month">
              <v-row align="center" justify="center" no-gutters>
                <div v-if="daysData[day].client.exercise" class="square center exercises">
                  <span v-if="daysData[day].trainer.exercise">&#10004;</span>
                </div>
                <div v-if="daysData[day].client.macros " class="square center macros">
                  <p v-if="daysData[day].trainer.macros">&#10004;</p>
                </div>
                <div v-if="daysData[day].client.steps" class="square center steps">
                  <p v-if="daysData[day].trainer.steps">&#10004;</p>
                </div>
              </v-row>
              <v-row align="center" justify="center" no-gutters>
                <div v-if="daysData[day].client.weight" class="square center weight">
                  <p v-if="daysData[day].trainer.weight">&#10004;</p>
                </div>
                <div v-if="daysData[day].client.payment" class="square center payment">
                  <p v-if="daysData[day].trainer.payment">&#10004;</p>
                </div>
                <div v-if="daysData[day].client.comment" class="square center comments">
                  <p v-if="daysData[day].trainer.comment">&#10004;</p>
                </div>
              </v-row>
            </div>
          </div>

          <v-row style="padding-top:15px" align="center" justify="center" no-gutters>
            <div v-if="(future === false && present === false) && daysData[day]">
              <v-btn
                style="background-color:grey; border-radius:40px"
                @click="viewData(day, month, year, weekday)"
                :small="true"
              >
                <v-icon>visibility</v-icon>
              </v-btn>
            </div>
            <div v-else-if="(future === false && present === false) && !daysData[day]">
              <v-btn
                style="background-color:grey; border-radius:40px"
                @click="viewData(day, month, year, weekday)"
                :small="true"
                disabled
              >
                <v-icon>visibility</v-icon>
              </v-btn>
            </div>
            <div v-else-if="present === true  && user.role == 3002 && daysData[day]">
              <v-btn
                style="background-color:grey; border-radius:40px"
                @click="addDataClient(day, month, year, weekday)"
                :small="true"
              >
                <v-icon>add</v-icon>
              </v-btn>
            </div>
            <div
              v-else-if="(future === true || present != true ) && user.role == 3002 && daysData[day]"
            >
              <v-btn
                style="background-color:grey; border-radius:40px"
                @click="addDataClient(day, month, year, weekday)"
                :small="true"
                disabled
              >
                <v-icon>add</v-icon>
              </v-btn>
            </div>
            <div
              v-else-if="(future === true || present === true) && (user.role ===3000 || user.role === 3001) && validDate(date)"
            >
              <v-btn
                style="background-color:grey; border-radius:40px"
                @click="addDateData(day, month, year ,present,future)"
                :small="true"
              >
                <v-icon>add</v-icon>
              </v-btn>
            </div>

            <div v-else>
              <v-btn
                style="background-color:grey; border-radius:40px"
                disabled
                @click="viewData(day, month, year, weekday)"
                :small="true"
              >
                <v-icon>clear</v-icon>
              </v-btn>
            </div>
          </v-row>
        </template>
      </v-calendar>
    </v-sheet>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import addData from "../components/addData";
import viewData from "../components/viewData";
import addDataClient from "../components/addDataClient";
import moment from "moment";
// import _ from "lodash";
import { extendMoment } from "moment-range";

export default {
  name: "Calendar",
  components: { addData, viewData, addDataClient },
  data() {
    return {
      focus: "",
      type: "month",
      dates: [],
      calendarMonth: 1,
      calendarYear: 0,
      calendarType: "",
      currentMonth: "",
      day: "",
      month: "",
      year: "",
      openAddData: false,
      openViewData: false,
      openAddDataClient: false,
      loading: false,
      eventMore: false,

      start: null,
      end: null,
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      today: "2020-05-01",
      daysData: {},
      events: [],
      colors: [
        "blue",
        "indigo",
        "deep-purple",
        "cyan",
        "green",
        "orange",
        "grey darken-1"
      ],
      names: [
        "Meeting",
        "Holiday",
        "PTO",
        "Travel",
        "Event",
        "Birthday",
        "Conference",
        "Party"
      ]
    };
  },

  computed: {
    ...mapGetters(["user", "activeClient", "getDayData", "daysExercises"]),
    monthFormatter() {
      return this.$refs.calendar.getFormatter({
        timeZone: "UTC",
        month: "long"
      });
    }
  },
  created() {
    this.checkCalendarType();
  },
  watch: {
    "$vuetify.breakpoint.name"() {
      this.checkCalendarType();
    },
    activeClient() {
      this.loading = true;
      this.checkDayExercises();
    },
    calendarYear() {
      this.$store.commit("setCalendarYear", this.calendarYear);
    },
    calendarMonth() {
      // console.log(this.$refs.calendar.renderProps.start.month);
      // console.log(this.$refs.calendar.renderProps.start.year);
      // console.log(this.$refs.calendar);
      this.$store.commit("setCalendarMonth", this.calendarMonth);
      const momentRange = extendMoment(moment);
      let firstDay = moment(
        `${this.calendarYear}-${this.calendarMonth}-01`,
        "YYYY-MM-DD"
      ).startOf("month");
      let endDay = moment(
        `${this.calendarYear}-${this.calendarMonth}-01`,
        "YYYY-MM-DD"
      ).endOf("month");

      let monthRange = momentRange.range(firstDay, endDay);

      let weeks = [];
      for (let mday of monthRange.by("days")) {
        if (weeks.indexOf(mday.week()) === -1) {
          weeks.push(mday.week());
        }
      }

      let calendar = [];
      for (let index = 0; index < weeks.length; index++) {
        var weeknumber = weeks[index];
        let firstWeekDay = moment(firstDay)
          .week(weeknumber)
          .day(0);
        if (firstWeekDay.isBefore(firstDay)) {
          firstWeekDay = firstDay;
        }
        let lastWeekDay = moment(endDay)
          .week(weeknumber)
          .day(6);
        if (lastWeekDay.isAfter(endDay)) {
          lastWeekDay = endDay;
        }
        let weekRange = momentRange.range(firstWeekDay, lastWeekDay);
        calendar.push(weekRange);
      }
      this.$store.commit("setWeekRanges", calendar);
    }
  },
  mounted() {
    this.calendarMonth = moment().month() + 1;
    this.loading = true;
    this.checkDayExercises();
  },
  methods: {
    checkCalendarType() {
      if (
        this.$vuetify.breakpoint.name == "xs" ||
        this.$vuetify.breakpoint.name == "sm"
      ) {
        if (this.calendarType == "month") {
          this.loading = true;
          this.checkDayExercises();
        }
        this.calendarType = "custom-weekly";
      } else {
        if (this.calendarType == "custom-weekly") {
          this.loading = true;
          this.checkDayExercises();
        }
        this.calendarType = "month";
      }
    },
    seeChange(data) {
      this.calendarMonth = data.start.month;
      this.calendarYear = data.start.year;
    },
    updateCalendar() {
      this.loading = true;
      this.checkDayExercises();
    },
    validDate(date) {
      if (this.activeClient) {
        return (
          moment(date).isSameOrAfter(this.activeClient.startDate) &&
          moment(date).isSameOrBefore(this.activeClient.endDate)
        );
      }
    },
    async checkDayExercises() {
      this.daysData = {};
      setTimeout(async () => {
        this.dates = document.querySelectorAll(".v-calendar-weekly__day-label");

        if (this.user.role == 3002) {
          await this.$store.dispatch("daysExercises", {
            month: this.calendarMonth,
            year: this.calendarYear
          });
        } else {
          if (this.activeClient) {
            const data = {
              user: this.activeClient.user,
              month: this.calendarMonth,
              year: this.calendarYear
            };

            await this.$store.dispatch("daysExercisesTrainerAdmin", data);
          }
        }
        this.dayHasExercise();
        if (this.daysExercises.length > 0) {
          this.daysExercises.forEach(el => {
            let day = moment(el.date).format("D");
            let month = moment(el.date).format("M");
            this.daysData[day] = {};
            this.daysData[day].month = month;
            this.daysData[day].trainer = el.trainer;
            this.daysData[day].client = el.client;
          });

          for (let i = 0; i < this.dates.length; i++) {
            if (
              this.dates[i].textContent ==
              moment()
                .startOf("month")
                .add(1, "months")
                .format("MMM D")
            ) {
              break;
            }

            this.daysExercises.find(day => {
              if (moment(day.date).format("D") == this.dates[i].textContent) {
                this.dates[i].classList.add("dayHasExercises");
                return true;
              }
            });
          }
        }
        if (this.user.role == 3000 || this.user.role == 3001) {
          if (this.activeClient) {
            this.$store.dispatch("getChartsAdminTrainer", {
              user: this.activeClient.user,
              month: this.calendarMonth,
              year: this.calendarYear
            });
            this.$store.dispatch("getPaymentTrainerAdmin", {
              user: this.activeClient.user,
              month: this.calendarMonth,
              year: this.calendarYear
            });
          }
        } else if (this.user.role == 3002) {
          this.$store.dispatch("getChartsClient", {
            month: this.calendarMonth,
            year: this.calendarYear
          });

          this.$store.dispatch("getPayment", {
            month: this.calendarMonth,
            year: this.calendarYear
          });
        }

        this.loading = false;
      }, 100);
    },
    dayHasExercise() {
      this.dates.forEach(el => {
        if (el.classList.contains("dayHasExercises")) {
          el.classList.remove("dayHasExercises");
        }
      });
    },
    closeAddData() {
      this.openAddData = false;
    },
    closeViewData() {
      this.openViewData = false;
    },
    closeAddDataClient() {
      this.openAddDataClient = false;
    },
    async addDataClient(day, month, year) {
      this.loading = true;
      this.day = day;
      this.month = month;
      this.year = year;

      let date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      date = moment(date).format("YYYY-MM-DD");

      await this.$store.dispatch("getExercisesClient", date);
      await this.$store.dispatch("getMacrosClient", moment(date).isoWeekday());
      await this.$store.dispatch("clientInput", date);

      if (this.getDayData.date) {
        this.loading = false;
        this.openAddDataClient = true;
      } else {
        this.loading = false;
      }
    },
    async viewData(day, month, year) {
      this.loading = true;
      this.day = day;
      this.month = month;
      this.year = year;

      const date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();
      const data = {
        user: this.activeClient.user,
        date: moment(date).format("YYYY-MM-DD"),
        day: moment(date).isoWeekday()
      };

      if (this.user.role == 3000 || this.user.role == 3001) {
        await this.$store.dispatch("getExercises", data);

        await this.$store.dispatch("trainerGetMacrosClient", data);

        await this.$store.dispatch("trainerClientInput", data);
      } else {
        await this.$store.dispatch("getExercisesClient", data.date);
        await this.$store.dispatch(
          "getMacrosClient",
          moment(date).isoWeekday()
        );
        await this.$store.dispatch("clientInput", data.date);
      }

      this.openViewData = true;
      this.loading = false;
    },
    async addDateData(day, month, year) {
      this.loading = true;

      this.day = day;
      this.month = month;
      this.year = year;

      const date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();
      const data = {
        user: this.activeClient.user,
        date: moment(date).format("YYYY-MM-DD")
      };

      this.openAddData = true;
      await this.$store.dispatch("getExercises", data);
      await this.$store.dispatch("getMacros", this.activeClient.user);
      this.loading = false;
    },
    showData(day) {
      console.log(day);
    },
    viewDay(data) {
      console.log(data);
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = this.today;
    },
    prev() {
      this.$refs.calendar.prev();

      // console.log();
      // console.log(data.start.year);
      this.loading = true;
      setTimeout(() => {
        this.checkDayExercises();
      }, 1000);
      // this.checkDayExercises();
    },
    next() {
      this.$refs.calendar.next();

      this.loading = true;
      setTimeout(() => {
        this.checkDayExercises();
      }, 1000);
      // this.checkDayExercises();
    },
    nth(d) {
      return d > 3 && d < 21
        ? "th"
        : ["th", "st", "nd", "rd", "th", "th", "th", "th", "th", "th"][d % 10];
    }
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

.dayHasExercises {
  background-color: yellow !important;
}

.center {
  text-align: center;
  padding: 0px 0;
}

.square {
  height: 23px;
  width: 23px;
  border-radius: 7px;
}

.exercises {
  background-color: #4dd7fa !important;
}

.macros {
  background-color: #e93578 !important;
}

.steps {
  background-color: #ffa834 !important;
}

.weight {
  background-color: #629c44 !important;
}

.payment {
  background-color: #f4eb49 !important;
}

.comments {
  background-color: #006ce5 !important;
}
</style>
