<script>
import { mapGetters } from "vuex";
import moment from "moment";

export default {
  name: "addData",
  props: ["show", "day", "month", "year"],
  data() {
    return {
      loading: false
    };
  },
  computed: mapGetters([
    "user",
    "types",
    "values",
    "activeClient",
    "macros",
    "getDayData"
  ]),

  methods: {
    addCalories(index) {
      if (this.user.systemType == 1000) {
        this.macros[index].calories =
          this.macros[index].proteins * 4 +
          this.macros[index].carbs * 4 +
          this.macros[index].fats * 9;
      }
    },
    async saveDay() {
      this.loading = true;
      const date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      this.getDayData.date = moment(date).format("YYYY-MM-DD");
      this.getDayData.user = this.activeClient.user;
      this.getDayData.macros = { ...this.macros };
      console.log(this.getDayData);

      await this.$store.dispatch("uploadDayData", this.getDayData);
      this.$emit("updateCalendar");
      this.loading = false;
    },
    close() {
      // this.client = {};
      // this.$refs.form.resetValidation();
      this.$emit("closed", this.show);
    },
    addWarmUp() {
      this.getDayData.warmUpExercises.push({
        name: "",
        value: undefined,
        option: ""
      });
    },
    addWorkout() {
      this.getDayData.workoutExercises.push({
        name: "",
        value: undefined,
        option: ""
      });
    },
    addCoolUp() {
      this.getDayData.coolUpExercises.push({
        name: "",
        value: undefined,
        option: ""
      });
    },
    deleteWarmUp(index) {
      this.getDayData.warmUpExercises.splice(index, 1);
    },
    deleteWorkout(index) {
      this.getDayData.workoutExercises.splice(index, 1);
    },
    deleteCoolUp(index) {
      this.getDayData.coolUpExercises.splice(index, 1);
    }
  }
};
</script>
<style scoped>
/** If i remove the scoped all the css doesnt work except the css for the input with type number */
