<template>
  <v-content class="container">
    <v-row align="stretch">
      <v-col id="columnForCharts">
        <div class="chart-container">
          <canvas height="165" class="chart" id="firstChart"></canvas>
        </div>
        <div class="chart-container">
          <canvas height="165" class="chart" id="secondChart"></canvas>
        </div>

        <div class="chart-container">
          <canvas height="165" class="chart" id="thirdChart"></canvas>
        </div>
      </v-col>
    </v-row>
  </v-content>
</template>

<script>
import Chart from "chart.js";
import { mapGetters } from "vuex";
import moment from "moment";

export default {
  name: "Charts",
  data() {
    return {
      stepsLabels: [],
      macrosLabels: [],
      weightLabels: [],
      dataSteps: [],
      dataProteins: [],
      dataCarbs: [],
      dataFat: [],
      dataWeight: [],
      firstChart: {},
      secondChart: {},
      thirdChart: {},
      firstTime: true
    };
  },
  mounted() {
    this.dataSteps = [0, 0, 0, 0, 0, 0, 0];
    this.dataProteins = [0, 0, 0, 0, 0, 0, 0];
    this.dataCarbs = [0, 0, 0, 0, 0, 0, 0];
    this.dataFat = [0, 0, 0, 0, 0, 0, 0];
    this.dataWeight = [0, 0, 0, 0, 0, 0, 0];

    this.createCharts();
  },
  computed: {
    ...mapGetters(["charts"])
  },
  watch: {
    charts() {
      this.dataSteps = [];
      this.dataProteins = [];
      this.dataCarbs = [];
      this.dataFat = [];
      this.dataWeight = [];
      this.stepsLabels = [];
      this.macrosLabels = [];
      this.weightLabels = [];

      if (this.charts.length > 0) {
        this.charts.forEach(el => {
          let dateExist = false;

          this.dataSteps.push(el.steps);
          this.stepsLabels.push(moment(el.date).format("YYYY-MM-DD"));

          this.dataProteins.push(el.protein);
          this.macrosLabels.push(moment(el.date).format("YYYY-MM-DD"));
          dateExist = true;

          this.dataCarbs.push(el.carbs);
          if (dateExist == false) {
            this.macrosLabels.push(moment(el.date).format("YYYY-MM-DD"));
            dateExist = true;
          }

          this.dataFat.push(el.fat);
          if (dateExist == false) {
            this.macrosLabels.push(moment(el.date).format("YYYY-MM-DD"));
            dateExist = true;
          }
          this.dataWeight.push(el.weight);

          this.weightLabels.push(moment(el.date).format("YYYY-MM-DD"));
        });
      } else {
        this.dataSteps = [0, 0, 0, 0, 0, 0, 0];
        this.dataProteins = [0, 0, 0, 0, 0, 0, 0];
        this.dataCarbs = [0, 0, 0, 0, 0, 0, 0];
        this.dataFat = [0, 0, 0, 0, 0, 0, 0];
        this.dataWeight = [0, 0, 0, 0, 0, 0, 0];
      }

      this.createCharts();
    },
    "$i18n.locale"() {
      this.createCharts();
    }
  },
  methods: {
    createCharts: function() {
      if (this.firstTime) {
        this.firstTime = false;
      } else {
        let firstChart = document.getElementById("firstChart");
        let secondChart = document.getElementById("secondChart");

        let thirdChart = document.getElementById("thirdChart");

        firstChart.remove();
        secondChart.remove();
        thirdChart.remove();

        document.getElementById(
          "columnForCharts"
        ).innerHTML = `<div class="chart-container">
          <canvas height="165" class="chart" id="firstChart"></canvas>
        </div>
        <div class="chart-container">
          <canvas height="165" class="chart" id="secondChart"></canvas>
        </div>

        <div class="chart-container">
          <canvas height="165" class="chart" id="thirdChart"></canvas>
        </div>`;
      }

      let firstChart = document.getElementById("firstChart").getContext("2d");
      let secondChart = document.getElementById("secondChart").getContext("2d");
      let thirdChart = document.getElementById("thirdChart").getContext("2d");

      this.firstChart = new Chart(firstChart, {
        type: "line",

        data: {
          labels: this.macrosLabels,
          datasets: [
            {
              label: this.$i18n.t("charts.protein"),
              data: this.dataProteins,
              borderColor: "green",
              backgroundColor: "green",
              fill: false,
              maintainAspectRatio: false
            },
            {
              label: this.$i18n.t("charts.carbohydrates"),
              data: this.dataCarbs,
              borderColor: "blue",
              backgroundColor: "blue",
              fill: false,
              maintainAspectRatio: false
            },
            {
              label: this.$i18n.t("charts.fat"),
              data: this.dataFat,
              borderColor: "purple",
              backgroundColor: "purple",
              fill: false,
              maintainAspectRatio: false
            }
          ]
        },
        options: { responsive: true }
      });

      this.secondChart = new Chart(secondChart, {
        type: "line",

        data: {
          labels: this.weightLabels,
          datasets: [
            {
              label: this.$i18n.t("charts.weight"),
              data: this.dataWeight,
              borderColor: "green",
              backgroundColor: "green",
              fill: false,
              maintainAspectRatio: false
            }
          ]
        },
        options: { responsive: true }
      });

      this.thirdChart = new Chart(thirdChart, {
        type: "line",

        data: {
          labels: this.stepsLabels,
          datasets: [
            {
              label: this.$i18n.t("charts.steps"),
              data: this.dataSteps,
              borderColor: "green",
              backgroundColor: "green",
              fill: false,
              maintainAspectRatio: false
            }
          ]
        },
        options: { responsive: true }
      });
    }
  }
};
</script>

<style >
.chart-container {
  position: relative;
  margin: auto;
}
.chart {
  height: 21.5vh;
}
.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
</style>