import { mapGetters } from "vuex";
import moment from "moment";
import axios from "axios";
import Cookies from "js-cookie";

export default {
  name: "viewData",
  props: ["show", "day", "month", "year"],
  data() {
    return {
      loading: false,
      loadVideo: false,
      verifyPaymentValue: false,
      exerciseId: 0,
      value: 0,
      video: false,
      videoUrl: "",
      viewExercise: ""
    };
  },
  computed: mapGetters([
    "types",
    "values",
    "activeClient",
    "macros",
    "user",
    "getDayData",
    "clientInput",
    "payment"
  ]),
  watch: {
    verifyPaymentValue() {
      if (this.verifyPaymentValue == true) {
        this.$store.dispatch("verifyPayment", {
          user: this.activeClient.user,
          date: moment(`${this.year}-${this.month}-${this.day}`).format(
            "YYYY-MM-DD"
          )
        });
      }
    }
  },
  methods: {
    updateDay(tab) {
      if (this.user.role == 3000 || this.user.role == 3001) {
        const date = moment(
          `${this.year}-${this.month}-${this.day}`,
          "YYYY-MM-DD"
        ).format("YYYY-MM-DD");

        const data = {
          type: tab,
          date,
          user: this.activeClient.user
        };

        this.$store.dispatch("updateDay", data);

        this.$emit("updateCalendar");
      }
    },
    seeVideoWarmUp(exercise, exerciseName) {
      if (this.user.role == 3002) {
        this.videoUrl = `/api/dayClient/exercises/video/warmUp/${this.year}/${
          this.month
        }/${this.day}/${exercise}?auth=${Cookies.get("token")}`;
      } else {
        this.videoUrl = `/api/trainer/exercises/video/warmUp/${
          this.activeClient.user
        }/${this.year}/${this.month}/${this.day}/${exercise}?auth=${Cookies.get(
          "token"
        )}`;
      }
      this.viewExercise = exerciseName;
      this.video = true;
    },
    seeVideoWorkout(exercise, exerciseName) {
      if (this.user.role == 3002) {
        this.videoUrl = `/api/dayClient/exercises/video/workout/${this.year}/${
          this.month
        }/${this.day}/${exercise}?auth=${Cookies.get("token")}`;
      } else {
        this.videoUrl = `/api/trainer/exercises/video/workout/${
          this.activeClient.user
        }/${this.year}/${this.month}/${this.day}/${exercise}?auth=${Cookies.get(
          "token"
        )}`;
      }
      this.viewExercise = exerciseName;
      this.video = true;
    },
    seeVideoCoolUp(exercise, exerciseName) {
      if (this.user.role == 3002) {
        this.videoUrl = `/api/dayClient/exercises/video/coolUp/${this.year}/${
          this.month
        }/${this.day}/${exercise}?auth=${Cookies.get("token")}`;
      } else {
        this.videoUrl = `/api/trainer/exercises/video/coolUp/${
          this.activeClient.user
        }/${this.year}/${this.month}/${this.day}/${exercise}?auth=${Cookies.get(
          "token"
        )}`;
      }
      this.viewExercise = exerciseName;
      this.video = true;
    },
    closeVideo() {
      this.video = false;
    },
    pickVideoWarmUp(index, exercise) {
      console.log("Warm up");
      this.exerciseId = exercise;
      this.$refs.videoWarmUp[index].click();
    },
    pickVideoWorkout(index, exercise) {
      console.log("Workout");
      this.exerciseId = exercise;
      this.$refs.videoWorkout[index].click();
    },
    pickVideoCoolUp(index, exercise) {
      console.log("Cool up");
      this.exerciseId = exercise;
      this.$refs.videoCoolUp[index].click();
    },
    onFilePickedWarmUp: async function(event) {
      // this.load = true;
      // await this.$store
      //   .dispatch("uploadImage", event.target.files[0])
      //   .then(async () => {
      //     await this.$store.dispatch("profile").then(() => {
      //       this.load = false;
      //     });
      //   });
      this.loadVideo = true;
      const formData = new FormData();
      formData.append(
        "video",
        event.target.files[0],
        event.target.files[0].name
      );
      await axios.post(
        `/api/dayClient/exercises/video/warmUp/${this.year}/${this.month}/${this.day}/${this.exerciseId}`,
        formData,
        {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`
          },
          onUploadProgress: progressEvent => {
            this.value = Math.round(
              (progressEvent.loaded / progressEvent.total) * 100
            );
            console.log(this.value);
          }
        }
      );
      this.$emit("updateCalendar");
      this.loadVideo = false;
      this.value = 0;
    },
    onFilePickedWorkout: async function(event) {
      this.loadVideo = true;
      const formData = new FormData();
      formData.append(
        "video",
        event.target.files[0],
        event.target.files[0].name
      );
      await axios.post(
        `/api/dayClient/exercises/video/workout/${this.year}/${this.month}/${this.day}/${this.exerciseId}`,
        formData,
        {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`
          },
          onUploadProgress: progressEvent => {
            this.value = Math.round(
              (progressEvent.loaded / progressEvent.total) * 100
            );
          }
        }
      );
      this.$emit("updateCalendar");
      this.loadVideo = false;
      this.value = 0;
    },
    onFilePickedCoolUp: async function(event) {
      this.loadVideo = true;
      const formData = new FormData();
      formData.append(
        "video",
        event.target.files[0],
        event.target.files[0].name
      );
      await axios.post(
        `/api/dayClient/exercises/video/coolUp/${this.year}/${this.month}/${this.day}/${this.exerciseId}`,
        formData,
        {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`
          },
          onUploadProgress: progressEvent => {
            this.value = Math.round(
              (progressEvent.loaded / progressEvent.total) * 100
            );
          }
        }
      );
      this.$emit("updateCalendar");
      this.loadVideo = false;
      this.value = 0;
    },

    close: function() {
      //   this.client = {};
      //   this.$refs.form.resetValidation();
      this.$emit("closed", this.show);
    },

    async saveData() {
      // this.loading = true;
      // this.clientData = { ...this.clientInput };
      // this.clientData.day = this.day;
      // const date = new Date(
      //   `${this.year}-${this.month}-${this.day}`
      // ).toISOString();
      // this.clientData.date = moment(date).format("YYYY-MM-DD");
      // this.clientData.user = this.user.user;
      // this.clientData.macros = { ...this.macros.client };
      // await this.$store.dispatch("uploadDayClient", this.clientData);
      // this.loading = false;
    },
    addWarmUp: function() {
      this.warmUpExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    addWorkout: function() {
      this.workOutExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    addCoolUp: function() {
      this.coolUpExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    deleteWarmUp: function(index) {
      this.warmUpExercises.splice(index, 1);
    },
    deleteWorkout: function(index) {
      this.workOutExercises.splice(index, 1);
    },
    deleteCoolUp: function(index) {
      this.coolUpExercises.splice(index, 1);
    }
  }
};
</script>

